import { useState } from "react";
import { Link as RouterLink } from "react-router-dom";
import { format } from "date-fns";
import numeral from "numeral";
import PropTypes from "prop-types";
import {
  Box,
  Button,
  Card,
  Checkbox,
  IconButton,
  InputAdornment,
  Link,
  Stack,
  Table,
  TableBody,
  TableCell,
  TableHead,
  TablePagination,
  TableRow,
  TextField,
  Typography,
} from "@material-ui/core";
import axios from "axios";
import SearchIcon from "../../icons/Search";
import ArrowRightIcon from "../../icons/ArrowRight";
import Dialoge from "../Dialog/Dialoge";
// import Label from "../../../components/Label";
import Scrollbar from "../../components/Scrollbar";
import userService from "../apis/userService";
import customAxios from "../apis/Api";
// import Dialog from "../feereport/FeeReport";

const sortOptions = [
  {
    label: "Newest first",
    value: "createdAt|desc",
  },
  {
    label: "Oldest first",
    value: "createdAt|asc",
  },
];

const getStatusLabel = (invoiceStatus) => {
  //   console.log("sta", invoiceStatus);
  const map = {
    canceled: {
      color: "error",
      text: "Canceled",
    },
    paid: {
      color: "success",
      text: "Paid",
    },
    pending: {
      color: "warning",
      text: "Pending",
    },
  };

  const { text, color } = map[invoiceStatus];

  return <Label color={color}>{text}</Label>;
};

const applyFilters = (invoices, query, filters) =>
  invoices.filter((invoice) => {
    let matches = true;

    if (query) {
      const properties = ["name", "phone"];
      let containsQuery = false;

      properties.forEach((property) => {
        if (invoice[property].toLowerCase().includes(query.toLowerCase())) {
          containsQuery = true;
        }
      });

      if (!containsQuery) {
        matches = false;
      }
    }

    if (filters.status && invoice.status !== filters.status) {
      matches = false;
    }

    return matches;
  });

const applyPagination = (invoices, page, limit) =>
  invoices.slice(page * limit, page * limit + limit);

const TransportList = (props) => {
  const { invoices, refresh, setRefresh, ...other } = props;
  //   console.log("invoices", invoices);
  const [selectedInvoices, setSelectedInvoices] = useState([]);
  const [page, setPage] = useState(0);
  const [limit, setLimit] = useState(10);
  const [query, setQuery] = useState("");
  const [sort, setSort] = useState(sortOptions[0].value);
  const [filters, setFilters] = useState({
    status: null,
  });

  const handleQueryChange = (event) => {
    setQuery(event.target.value);
  };

  const handleSortChange = (event) => {
    setSort(event.target.value);
  };

  const handleSelectAllInvoices = (event) => {
    // console.log("1",event.target.checked);
    setSelectedInvoices(
      event.target.checked ? invoices.map((invoice) => invoice.id) : []
    );
  };

  const removeHandler = () => {
    const text = confirm(`Are You Sure You Want to Delete `);

    if (text == true) {
      let idstring = "";
      selectedInvoices.forEach((e) => {
        idstring = e + "," + idstring;
      });
      idstring = idstring.slice(0, -1);
      const obj = {
        id: idstring,
      };
      customAxios().delete(`http://localhost:8000/tranport/delete`, { data: obj })
        .then((res) => {
          res.data.json;
          setRefresh(!refresh);
          setSelectedInvoices([]);
        });
    } else {
      setSelectedInvoices([...selectedInvoices]);
    }

    // console.log(Object.assign({},selectedInvoices));
    // console.log(selectedInvoices.reduce((a,v)=>({...a,[v]:v}),{}));
  };

  const handleSelectOneInvoice = (event, invoiceId) => {
    // console.log("2", invoiceId);
    if (!selectedInvoices.includes(invoiceId)) {
      setSelectedInvoices((prevSelected) => [...prevSelected, invoiceId]);
    } else {
      setSelectedInvoices((prevSelected) =>
        prevSelected.filter((id) => id !== invoiceId)
      );
    }
  };

  const handlePageChange = (event, newPage) => {
    setPage(newPage);
  };

  const handleLimitChange = (event) => {
    setLimit(parseInt(event.target.value, 10));
  };

  // Usually query is done on backend with indexing solutions
  const filteredInvoices = applyFilters(invoices, query, filters);
  const paginatedInvoices = applyPagination(filteredInvoices, page, limit);
  const enableBulkActions = selectedInvoices.length > 0;
  const selectedSomeInvoices =
    selectedInvoices.length > 0 && selectedInvoices.length < invoices.length;
  const selectedAllInvoices = selectedInvoices.length === invoices.length;

  return (
    <Card {...other}>
      <Box
        sx={{
          alignItems: "center",
          display: "flex",
          flexWrap: "wrap",
          m: -1,
          p: 2,
        }}
      >
        <Box
          sx={{
            m: 1,
            maxWidth: "100%",
            width: 500,
          }}
        >
          <TextField
            fullWidth
            InputProps={{
              startAdornment: (
                <InputAdornment position="end">
                  <SearchIcon fontSize="small" />
                </InputAdornment>
              ),
            }}
            onChange={handleQueryChange}
            placeholder="Search invoices by Student Name"
            value={query}
            variant="outlined"
          />
        </Box>
        <Box
          sx={{
            m: 1,
            maxWidth: "100%",
            width: 240,
          }}
        >
          <TextField
            fullWidth
            label="Sort By"
            name="sort"
            onChange={handleSortChange}
            select
            SelectProps={{ native: true }}
            value={sort}
            variant="outlined"
          >
            {sortOptions.map((option) => (
              <option key={option.value} value={option.value}>
                {option.label}
              </option>
            ))}
          </TextField>
        </Box>
      </Box>
      {enableBulkActions && (
        <Box sx={{ position: "relative" }}>
          <Stack direction="row">
            <Box
              sx={{
                backgroundColor: "background.paper",
                mt: "6px",
                position: "absolute",
                px: "4px",
                width: "100%",
                zIndex: 2,
              }}
            >
              <Checkbox
                checked={selectedAllInvoices}
                color="primary"
                indeterminate={selectedSomeInvoices}
                onChange={handleSelectAllInvoices}
              />

              <Button
                color="primary"
                sx={{ ml: 2 }}
                variant="outlined"
                onClick={removeHandler}
              >
                Delete
              </Button>
              <Button color="primary" sx={{ ml: 2 }} variant="outlined">
                Edit
              </Button>
            </Box>
          </Stack>
        </Box>
      )}

      <Scrollbar>
        <Box sx={{ minWidth: 1200 }}>
          <Table>
            <TableHead>
              <TableRow>
                <TableCell padding="checkbox">
                  <Checkbox
                    checked={selectedAllInvoices}
                    color="primary"
                    indeterminate={selectedSomeInvoices}
                    onChange={handleSelectAllInvoices}
                  />
                </TableCell>
                <TableCell>Name</TableCell>
                <TableCell>Mobile</TableCell>
                <TableCell>LICENSE NO</TableCell>
                <TableCell>VEHICLE NO</TableCell>
                <TableCell>ROUTE NAME</TableCell>
                {/* <TableCell>Action</TableCell> */}
              </TableRow>
            </TableHead>
            <TableBody>
              {paginatedInvoices.map((invoice) => {
                const isInvoiceSelected = selectedInvoices.includes(invoice.id);
                // console.log("inv", isInvoiceSelected);
                // if (invoice.id == "") {
                //   {
                //     ("data not found");
                //   }
                // } else {
                  return (
                    <TableRow
                      hover
                      key={invoice.id}
                      selected={isInvoiceSelected}
                    >
                      <TableCell padding="checkbox">
                        <Checkbox
                          checked={isInvoiceSelected}
                          color="primary"
                          onChange={(event) =>
                            handleSelectOneInvoice(event, invoice.id)
                          }
                          value={isInvoiceSelected}
                        />
                      </TableCell>
                      <TableCell>
                        <Link
                          color="textPrimary"
                          component={RouterLink}
                          to="#"
                          underline="none"
                          variant="subtitle2"
                        >
                          {invoice.name}
                        </Link>
                        <Typography color="textSecondary" variant="body2">
                          {invoice.email}
                        </Typography>
                      </TableCell>
                      <TableCell>{invoice.phone}</TableCell>
                      {/* <TableCell>{getStatusLabel(invoice.status)}</TableCell> */}

                      <TableCell>{invoice.License_no}</TableCell>
                      <TableCell>{invoice.vehicle_no}</TableCell>
                      <TableCell>{invoice.route_name}</TableCell>
                      {/* <TableRow>
                      <IconButton
                        title="Edit Item"
                        component={RouterLink}
                        to="./TransportForm"
                        state={{ ...invoice }}
                      >
                        <ArrowRightIcon fontSize="small" />
                      </IconButton>
                    </TableRow> */}
                    </TableRow>
                  );
                // }
              })}
            </TableBody>
          </Table>
        </Box>
      </Scrollbar>
      <TablePagination
        component="div"
        count={filteredInvoices.length}
        onPageChange={handlePageChange}
        onRowsPerPageChange={handleLimitChange}
        page={page}
        rowsPerPage={limit}
        rowsPerPageOptions={[5, 10, 25]}
      />
    </Card>
  );
};

TransportList.propTypes = {
  invoices: PropTypes.array.isRequired,
};

export default TransportList;
