import { useState, useEffect, useCallback } from "react";
import { useParams,Link as RouterLink } from "react-router-dom";
import { Helmet } from "react-helmet-async";
import {
  Box,
  Breadcrumbs,
  Button,
  Container,
  Grid,
  Link,
  Typography,
} from "@material-ui/core";
import TransportList from "./TransportList";
import useSettings from "../../hooks/useSettings";
import axios from "axios";
import gtm from "../../lib/gtm";
import Header_Design from "./Header_Design";
import userService from "../apis/userService";
import {customAxios} from "../apis/Axios";


const Transport = () => {
  const { settings } = useSettings();
const { id } = useParams();
console.log(id)
  const [rows, setrows] = useState([]);
  const [open, setOpen] = useState(false);
  const [refresh,setRefresh]=useState(false)
  useEffect(async () => {
    const data = await customAxios.getCall(`tranport/transports`);
    setrows(data);
  }, [open, refresh]);

  useEffect(() => {
    gtm.push({ event: "page_view" });
  }, []);

  return (
    <>
      <Helmet>
        <title>Dashboard: Invoice List | Material Kit Pro</title>
      </Helmet>
      <Box
        sx={{
          backgroundColor: "background.default",
          minHeight: "100%",
          py: 8,
        }}
      >
        <Container maxWidth={settings.compact ? "xl" : false}>
          <Header_Design
            rows={rows}
            open={open}
            setOpen={setOpen}
            text="Transport"
            dashText="Transport Detail"
          />
          <Box sx={{ mt: 3 }}>
            <TransportList
              setRefresh={setRefresh}
              refresh={refresh}
              invoices={rows}
            />
          </Box>
        </Container>
      </Box>
    </>
  );
};

export default Transport;
